import React, { useEffect, useRef } from "react";
import maplibregl from "maplibre-gl";
import "maplibre-gl/dist/maplibre-gl.css";

export default function MapView() {
  const mapContainer = useRef(null);
  const map = useRef(null);

  useEffect(() => {
    if (map.current) return;

    map.current = new maplibregl.Map({
      container: mapContainer.current,
      style: "https://api.maptiler.com/maps/openstreetmap/style.json?key=pXC2MulBPSvEsgk84x5E",
      center: [-118.95258684698637, 34.18334035884417],
      zoom: 16
    });

    map.current.on("load", () => {
      const markers = [
        [34.18518367221783, -118.95283325589428],
        [34.18324442509275, -118.9525757638468],
        [34.18329323964055, -118.95214661043437]
      ];

      markers.forEach(([lat, lon]) => {
        new maplibregl.Marker()
          .setLngLat([lon, lat])
          .addTo(map.current);
      });
    });
  }, []);

  return <div ref={mapContainer} className="w-2/3 h-full" id="map" />;
}